.info {
    background: #d5ff5d;
    color: #201c50;
    margin: auto;
    margin: 40px auto;
    border-radius: 25px;
    // margin-right: auto;
    width: 700px;
    padding: 0 100px;
    section {
        display: flex;
        flex-direction: column;
        margin-bottom: 50px;
    }
    h1 {
        font-size: 40px;
        margin-bottom: 10px;
        margin-top: 50px;
    }
    p {
        margin-bottom: 0px;
    }
}